h1,
h2,
h3,
h4,
p {
  margin: 0;
  padding: 0;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  text-decoration: none;
}


address{
  font-style: normal;
}

button {
  border: none;
  cursor: pointer;
  padding: 0;
}

img {
  display: block;
  height: auto;
  width: 100%;
  max-width: 100%;
}
